.prize{
    min-width: 100px;
    aspect-ratio: 39 / 89;
    background-image: url(../../img/prize/prize_logo.png);
    background-repeat: no-repeat;
    width: 8lvw;
    height: auto;
    background-clip: padding-box;
    background-size: contain;
    overflow: hidden;
    min-height: auto;
    display: inline-block;
    border-radius: .2rem;
    position: absolute;
    bottom: 0px;
    margin: 2rem;
    box-shadow: 0 0 16px #b2a7a921, 0 0 10px #6c66672e, 0 0 3px #cebfc21f;
    scale:1;
    z-index:1;
    transition: all .3s ease;
  }
  .prize::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(210deg, #00000070 0%, #0000 40%);
    z-index: 1;
    border-radius: .2rem;
    transition: background .3s ease;
  }


@media screen and (max-width: 425px) {.prize{
  min-width: 55px;
  margin: 1rem;
  margin-left: 0rem;
  bottom:34lvh;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;}}

@media screen and (min-width: 426px) and (max-width: 768px) {.prize{
  min-width: 55px;
  margin: 1rem;
  margin-right: 0rem;
  bottom:71lvh;
  right:0;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;}}

@media screen and (min-width: 769px) {.prize{
  min-width: 90px;
  max-width: 125px;
  margin: 1rem;
  margin-right: 0rem;
  bottom:71lvh;
  right:0;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;}}

@media screen and (min-width: 1024px) {.prize{
  min-width: 90px;
  max-width: 125px;
  margin: 1rem;
  margin-right: 0rem;
  bottom:30lvh;
  right:-.5rem;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;}

  .prize:hover{
    right:0;
}}